/* open-sans-300 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: local(""), url("../fonts/open-sans-v26-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/open-sans-v26-latin_cyrillic-300.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/open-sans-v26-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v26-latin_cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* open-sans-600 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  src: local(""), url("../fonts/open-sans-v26-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/open-sans-v26-latin_cyrillic-600.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
body {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}
.page--modal-open {
  overflow-y: hidden;
  height: 100vh;
}

.page-main {
  flex: 1 0 auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  background-color: #000000;
}
.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.main-header {
  position: absolute;
  width: 100%;
}
.main-header__row {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1023px) {
  .main-header__row {
    justify-content: flex-end;
  }
}

.no-js {
  position: relative;
}

.menu {
  z-index: 3;
}
.menu .active {
  z-index: 15;
}
@media (max-width: 1023px) {
  .menu__burger {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    cursor: pointer;
    background-color: #0ad9c6;
    padding: 18px;
    z-index: 10;
  }
  .menu__burger:hover {
    background-color: rgba(10, 217, 198, 0.7);
  }
  .menu__burger.no-js {
    display: none;
  }
  .menu__burger span {
    position: absolute;
    top: 24.75px;
    left: 13.5px;
    width: 22.5px;
    height: 1px;
    background-color: #000000;
  }
  .menu__burger span:first-child {
    top: 18.5px;
  }
  .menu__burger span:last-child {
    top: auto;
    bottom: 18px;
  }
  .menu__burger.active {
    background-color: transparent;
  }
  .menu__burger.active:hover {
    background-color: rgba(201, 204, 212, 0.8);
  }
  .menu__burger.active span {
    background-color: rgba(20, 21, 23, 0.2);
    transform: scale(0);
  }
  .menu__burger.active span:first-child {
    transform: rotate(45deg);
    top: 24px;
  }
  .menu__burger.active span:last-child {
    transform: rotate(-45deg);
    bottom: 24.5px;
  }
}

nav {
  padding: 0 21px 6px 19px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  margin: 18px 0 0 0;
  z-index: 5;
}
nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
nav ul li {
  margin-right: 135px;
}
nav ul li:last-child {
  margin-right: 0;
}
nav a {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: rgba(255, 255, 255, 0.7);
}
nav a:hover {
  color: rgba(201, 204, 212, 0.5);
}
nav a:focus {
  color: rgba(10, 217, 198, 0.7);
}

@media (max-width: 1023px) {
  nav {
    transform: translate(0, -100%);
    width: 320px;
    height: 100vh;
    padding: 100px 0 100px 0;
    margin: 0;
    background-color: #ffffff;
  }
  nav ul {
    display: flex;
    flex-direction: column;
  }
  nav ul li {
    display: flex;
    align-items: center;
    height: 60px;
    background-color: #f6f6f6;
    margin: 0 0 30px 0;
  }
  nav ul li::after {
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    right: 16px;
    background: url("../img/icons/arrow-right.png");
    cursor: pointer;
  }
  nav a {
    width: 100%;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    letter-spacing: normal;
    color: rgba(72, 73, 77, 0.7);
    padding-right: 0;
    padding-left: 16px;
  }
  nav a:hover {
    color: #1f1f1f;
  }
  nav.active {
    transform: translate(0, 0);
  }
  nav.no-js {
    transform: translate(0, 0);
  }
}
